<template>
  <div class="officeComplaint">
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="first">
        <template>
          <el-table :data="complaintPatientList" height="250" border style="width: auto">
            <el-table-column prop="name" label="" width="180"></el-table-column>
            <el-table-column prop="date" label="" width="180"></el-table-column>
            <el-table-column prop="containts" label="" width="500"></el-table-column>
            <el-table-column label="">
              <template scope="scope">
                <el-button size="small" @click.native.prevent="lookforPatient(scope.$index)"></el-button>
                <el-button size="small" type="danger" @click.native.prevent="delectPatient(scope.$index, complaintPatientList)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <div class="importants">
          <h1></h1>
          <h3></h3>
          <h2></h2>
            <ol>
              <li>(0511-88619092)(zjzyydb@163.com)()</li>
              <li>(18906100399)</li>
              <li>
              </li>
            </ol>
          <h2></h2>
            <ol>
              <li>(0511-8861901588619016)</li>
              <li>(0511-88619092)</li>
              <li> (0511-88619093)</li>
              <li>(0511-88619091)</li>
              <li>(0511-88619085)</li>
              <li>(0511-88619089)</li>
              <li>,(0511-88619087)</li>
              <li>(0511-88619086)</li>
              <li>(0511-88619090)</li>
              <li>(0511-88619077)</li>
              <li>(0511-88619084)</li>
              <li>(0511-88619078)</li>
              <li></li>
              <li></li>
            </ol>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="third">
        <el-input type="textarea" :rows="4" placeholder="" v-model="textarea">
        </el-input>
        <el-button type="danger" @click="getEmpty"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </el-tab-pane>
    </el-tabs>
    <el-dialog title="" :visible.sync="dialogFormVisible" :before-close="handleClose">
      <ul class="patientMessage">
        <li><h3>:</h3><span>{{messages.name}}</span></li>
        <li><h3>:</h3><span>{{messages.number}}</span></li>
        <li><h3>:</h3><span>{{messages.sex}}</span></li>
        <li><h3>:</h3><span>{{messages.age}}</span></li>
        <li><h3>:</h3><span>{{messages.disease}}</span></li>
        <li><h3>:</h3><span>{{messages.symptoms}}</span></li>
        <li><h3>:</h3><span>{{messages.phone}}</span></li>
        <li><h3>:</h3><span>{{messages.position}}</span></li>
        <li><h3>:</h3><span>{{messages.date}}</span></li>
      </ul>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
    export default {
      data () {
        return {
          dialogFormVisible: false,
          complaintPatientList: [],
          messages: [],
          patientList: [],
          activeName: 'second',
          Index: '',
          textarea: ''
        };
      },
      methods: {
        handleClose (done) {
          this.$confirm('')
            .then(_ => {
              done();
            })
            .catch(_ => {});
        },
        lookforPatient (index) {
          // console.log(index, row);
          this.dialogFormVisible = true;
          this.Index = index;             // mark
          // console.log('', this.patientList);
          // console.log('', this.complaintPatientList[this.Index].name);
          for (let i = 0; i < this.patientList.length; i++) {
            if (this.complaintPatientList[this.Index].name === this.patientList[i].name) {
              this.messages = this.patientList[i];
            };
          };
        },
        delectPatient (index, rows) {
          rows.splice(index, 1);
        },
        getEmpty () {
          this.textarea = '';
        },
        save () {
          if (this.textarea === '') {
            alert('');
          } else {
            alert('');
          };
        }
      },
      created () {
        this.$http.get('../../static/doctor/complaintPatient.json').then((response) => {             // mark
          this.complaintPatientList = response.body.complaintPatientList;
        }, response => {
          // error callback
          alert('');
        });
        this.$http.get('../../static/patient/registered.json').then((response) => {             // mark
          this.patientList = response.body.tableData;
        }, response => {
          // error callback
          alert('');
        });
      }
    };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.patientMessage li
  font-size:20px
.patientMessage h3
  width:150px
  line-height:40px
  display:inline-block
  margin-left:30px
  margin-right:50px
.patientMessage span
  color:#999
.importants h1
  font-size:22px
  margin:0 0 20px 0
.importants h2
  font-size:18px
  margin:10px 0 10px 0
.importants h3
  color:red
  font-size:14px
.importants li
  margin-left:30px
</style>



