<template>
  <div class="patientFeedback">
    <span class="title"></span>
    <div class="form">
      <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" required>
           <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 150px;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" required>
           <el-col :span="11">
            <el-form-item prop="date2">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.date2" style="width: 150px;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
      </el-form>
          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list1">
            <input
            type="checkbox"
            v-model="toggle1"
            name="list1"
            :true-value="item"
            :false-value="b">{{item.number1}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list2">
            <input
            type="checkbox"
            v-model="toggle2"
            name="list2"
            :true-value="item"
            :false-value="b">{{item.number2}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list3">
            <input
            type="checkbox"
            v-model="toggle3"
            name="list3"
            :true-value="item"
            :false-value="b">{{item.number3}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list4">
            <input
            type="checkbox"
            v-model="toggle4"
            name="list4"
            :true-value="item"
            :false-value="b">{{item.number4}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list5">
            <input
            type="checkbox"
            v-model="toggle5"
            name="list5"
            :true-value="item"
            :false-value="b">{{item.number5}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list6">
            <input
            type="checkbox"
            v-model="toggle6"
            name="list6"
            :true-value="item"
            :false-value="b">{{item.number6}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list7">
            <input
            type="checkbox"
            v-model="toggle7"
            name="list7"
            :true-value="item"
            :false-value="b">{{item.number7}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list8">
            <input
            type="checkbox"
            v-model="toggle8"
            name="list8"
            :true-value="item"
            :false-value="b">{{item.number8}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list9">
            <input
            type="checkbox"
            v-model="toggle9"
            name="list9"
            :true-value="item"
            :false-value="b">{{item.number9}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list10">
            <input
            type="checkbox"
            v-model="toggle10"
            name="list10"
            :true-value="item"
            :false-value="b">{{item.number10}}
            </label>
          </div>

          <div class="checkbox">
          <span class="question"></span>
            <label v-for="item in list11">
            <input
            type="checkbox"
            v-model="toggle11"
            name="list11"
            :true-value="item"
            :false-value="b">{{item.number11}}
            </label>
          </div>
      <el-form>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        list1: [
          {number1: ''},
          {number1: ''},
          {number1: ''}
        ],
        list2: [
          {number2: ''},
          {number2: ''},
          {number2: ''}
        ],
        list3: [
          {number3: ''},
          {number3: ''},
          {number3: ''}
        ],
        list4: [
          {number4: ''},
          {number4: ''},
          {number4: ''}
        ],
        list5: [
          {number5: ''},
          {number5: ''},
          {number5: ''}
        ],
        list6: [
          {number6: ''},
          {number6: ''},
          {number6: ''}
        ],
        list7: [
          {number7: ''},
          {number7: ''},
          {number7: ''}
        ],
        list8: [
          {number8: ''},
          {number8: ''},
          {number8: ''}
        ],
        list9: [
          {number9: ''},
          {number9: ''},
          {number9: ''}
        ],
        list10: [
          {number10: ''},
          {number10: ''},
          {number10: ''}
        ],
        list11: [
          {number11: ''},
          {number11: ''},
          {number11: ''}
        ],
        toggle1: {number1: ''},
        toggle2: {number2: ''},
        toggle3: {number3: ''},
        toggle4: {number4: ''},
        toggle5: {number5: ''},
        toggle6: {number6: ''},
        toggle7: {number7: ''},
        toggle8: {number8: ''},
        toggle9: {number9: ''},
        toggle10: {number10: ''},
        toggle11: {number11: ''},
        b: {},
        ruleForm: {
          date1: '',
          date2: ''
        },
        rules: {
          date1: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          date2: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ]
        }
      };
    },
    methods: {
      onSubmit (formName) {
        this.timeFormat();
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('!');
            console.log(this.ruleForm);
          } else {
            alert('!!');
            return false;
          }
        });
      },
      resetForm (formName) {
        this.$refs[formName].resetFields();
      },
      timeFormat () {
        let oldDate1 = this.ruleForm.date1;
        let oldDate2 = this.ruleForm.date2;
        let year1 = new Date(oldDate1).getFullYear();
        let month1 = new Date(oldDate1).getMonth() + 1;
        let day1 = new Date(oldDate1).getDate();
        let year2 = new Date(oldDate2).getFullYear();
        let month2 = new Date(oldDate2).getMonth() + 1;
        let day2 = new Date(oldDate2).getDate();
        let dateFormat1 = year1 + '-' + month1 + '-' + day1;
        let dateFormat2 = year2 + '-' + month2 + '-' + day2;
        console.log(dateFormat1);
        console.log(dateFormat2);
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .title
    font-size:24px
    margin-left:300px
  .form
     margin-top:80px
  .patientFeedback
    height:920px
    width:1000px
    margin:auto
    border:1px solid black
  .checkbox
    padding-bottom:50px
  .question
    padding-right:100px

</style>


