<template>
  <div class="editDoctor">
    <div class="search">
     <el-input @keyup.enter.native="" style="width: 200px;" placeholder="" v-model="edictDoctor.name"></el-input>
     <el-button  type="primary" icon="search" @click="" v-model="edictDoctor.name"></el-button>
    </div>
    <el-table :data="doctorlist" border style="width: 100%;">
      <el-table-column fixed prop="index" label="" width="100">
        <template scope="scope">
          {{scope.$index}}
        </template>
      </el-table-column>
      <el-table-column fixed prop="name" label="" width="150">
      </el-table-column>
      <el-table-column fixed prop="sex" label="" width="150"> 
      </el-table-column>
      <el-table-column fixed  label="" width="150">
        <template scope="scope">
          {{scope.row.date}}
        </template> 
      </el-table-column>
      <el-table-column fixed prop="degree" label="" width="150"> 
      </el-table-column>
      <el-table-column fixed prop="major" label="" width="150"> 
      </el-table-column>
      <el-table-column fixed prop="profession" label="" width="150"> 
      </el-table-column>
      <el-table-column fixed prop="office" label="" width="150"> 
      </el-table-column>
      <el-table-column fixed="right" prop="operate" label="" width="150"> 
        <template scope="scope">
          <el-button @click.native.prevent="edictDoctor(scope.$index)" type="text" size="small"></el-button>
          <el-button @click.native.prevent="delectDoctor(scope.$index, doctorlist)" type="text" size="small"></el-button>
        </template>
      </el-table-column>     
    </el-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="doctorlistedit.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label=""  :label-width="formLabelWidth">
          <el-radio class="radio" v-model="doctorlistedit.sex" label=""></el-radio>
          <el-radio class="radio" v-model="doctorlistedit.sex" label=""></el-radio>
        </el-form-item>
        <el-form-item label="" prop="date" :label-width="formLabelWidth">
            <el-date-picker type="date" placeholder="" v-model="doctorlistedit.date" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="doctorlistedit.degree" placeholder="" style="width: 100%;">
            <el-option label="" name="junior" value=""></el-option>
            <el-option label="" name="undergraduate" value=""></el-option>
            <el-option label="" name="postgraduate" value=""></el-option>
            <el-option label="" name="doctor" value=""></el-option>
            <el-option label="" name="master" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="doctorlistedit.major" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label=""  :label-width="formLabelWidth">
          <el-input v-model="doctorlistedit.profession" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label=""  :label-width="formLabelWidth">
          <el-input v-model="doctorlistedit.office" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="updateDoctor"> </el-button>
      </div>
    </el-dialog>
    <div class="block">
      <span class="demonstration"></span>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="sizes, prev, pager, next" :total="100">
      </el-pagination>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
    export default {
      data () {
        return {
          doctorlist: [],
          doctorlistedit: [],
          dialogFormVisible: false,
          formLabelWidth: '60px',
          Index: '',
          currentPage: 5
        };
      },
      methods: {
        edictDoctor (index) {
          // console.log(index, row);
          this.dialogFormVisible = true;
          this.Index = index;             // mark
          this.doctorlistedit = this.doctorlist[index];
        },
        delectDoctor (index, rows) {
          console.log(rows[index]);
          rows.splice(index, 1);
        },
        updateDoctor () {
          this.dialogFormVisible = false;
          // mark get
          console.log(this.doctorlistedit.date);
          let year = this.doctorlistedit.date.getFullYear();
          let month = this.doctorlistedit.date.getMonth() + 1;
          let day = this.doctorlistedit.date.getDate();
          let dateFormat = year + '-' + month + '-' + day;
          // console.log(this.doctorlistedit);
          this.doctorlistedit.date = dateFormat;
          // this.$http.get('../../static/doctor.json', this.doctorlistedit).then(function () {
          this.doctorlist[this.Index] = this.doctorlistedit;
          // this.doctorlist[this.Index].date = dateFormat;
          this.$message({
            message: '',
            type: 'success'
          });
          console.log(this.doctorlistedit);
          console.log('---------', this.doctorlist[this.Index]);
          // }, function () {
          //   this.$message.error('');
          // });
        },
        handleSizeChange (val) {
          console.log(` ${val} `);
        },
        handleCurrentChange (val) {
          console.log(`: ${val}`);
        }
      },
      created () {
        this.$http.get('../../static/doctor/doctor.json').then((response) => {             // mark
          this.doctorlist = response.body.doctorlist;
        }, response => {
          // error callback
          alert('');
        });
      }
    };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.editDoctor .block
  position:absolute
</style>



