<template>
  <div class="shiftDoctor">
    <div class="search">
     <el-input  style="width: 200px;" placeholder=""></el-input>
     <el-button  type="primary" icon="search" @click=""></el-button>
    </div>
    <el-table :data="shiftList" border style="width: 100%;">
      <el-table-column fixed prop="date" label="" width="120">
        <template scope="scope">
          {{scope.row.date}}
        </template> 
      </el-table-column>
      <el-table-column fixed prop="dates" label="" width="200">
      </el-table-column>
      <el-table-column fixed prop="index" label="" width="80">
        <template scope="scope">
          {{scope.$index}}
        </template>
      </el-table-column>
      <el-table-column fixed prop="name" label="" width="120">
      </el-table-column>
      <el-table-column fixed prop="sex" label="" width="120"> 
      </el-table-column>
      <el-table-column fixed prop="office" label="" width="120"> 
      </el-table-column>
      <el-table-column fixed prop="number" label="" width="130"> 
      </el-table-column>
      <el-table-column fixed prop="attendance" label="" width="120"> 
      </el-table-column>
      <el-table-column fixed prop="signatory" label="" width="120"> 
      </el-table-column>
      <el-table-column fixed="right" prop="operate" label="" width="120"> 
        <template scope="scope">
          <el-button @click.native.prevent="edictDoctor(scope.$index, shiftList)" type="text" size="small"></el-button>
          <el-button @click.native.prevent="delectDoctor(scope.$index, shiftList)" type="text" size="small"></el-button>
        </template>
      </el-table-column>     
    </el-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">

      <el-form>
        <el-form-item label="" prop="date" :label-width="formLabelWidth">
            <el-date-picker type="date" placeholder="" v-model="form.date" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.dates" placeholder="" style="width: 100%;">
            <el-option label="6:00~17:00" name="lessMorning" value="6:00~17:00"></el-option>
            <el-option label="1000~2100" name="Morning" value="1000~2100"></el-option>
            <el-option label="1600~0000" name="lessNinght" value="1600~0000"></el-option>
            <el-option label="0000~800" name="Ninght" value="0000~800"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label=""  :label-width="formLabelWidth">
          <el-radio class="radio" v-model="form.sex" label=""></el-radio>
          <el-radio class="radio" v-model="form.sex" label=""></el-radio>
        </el-form-item>
        <el-form-item label=""  :label-width="formLabelWidth">
          <el-input v-model="form.office" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.attendance" placeholder="" style="width: 100%;">
            <el-option label="" name="late" value=""></el-option>
            <el-option label="" name="early" value=""></el-option>
            <el-option label="" name="bework" value=""></el-option>
            <el-option label="" name="overtime" value=""></el-option>
            <el-option label="" name="sickleave" value=""></el-option>
            <el-option label="" name="generalholiday" value=""></el-option>
            <el-option label="" name="others" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=""  :label-width="formLabelWidth">
          <el-input v-model="form.signatory" auto-complete="off"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <!-- <el-button @click="resetForm('form')"> </el-button> -->
        <!-- <el-button type="primary"  @click="submitForm(index,row)"> </el-button> -->
      </div>
    </el-dialog>
    <div class="block">
      <span class="demonstration"></span>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="sizes, prev, pager, next" :total="100">
      </el-pagination>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
    export default {
      data () {
        return {
          shiftList: [],
          // form
          Index: '',
          form: [],
          dialogFormVisible: false,
          formLabelWidth: '60px',
          currentPage: 5
        };
      },
      methods: {
        edictDoctor (index, rows) {
          // console.log(index, row);
          this.dialogFormVisible = true;
          // this.form = row;
          console.log(rows[index]);
          this.form = rows[index];
          this.form = JSON.parse(JSON.stringify(rows[index]));
          this.Index = index;
          // rows[index] = this.form;
        },
        delectDoctor (index, rows) {
          console.log(rows);
          rows.splice(index, 1);
        },
        updateDoctor () {
          this.dialogFormVisible = false;
          // mark get
          console.log(this.form.date);
          let year = this.form.date.getFullYear();
          let month = this.form.date.getMonth() + 1;
          let day = this.form.date.getDate();
          let dateFormat = year + '-' + month + '-' + day;
        //   // console.log(this.doctorlistedit);
          this.form.date = dateFormat;
        },
        //   // this.$http.get('../../static/doctor.json', this.doctorlistedit).then(function () {
        //   this.doctorlist[this.Index] = this.doctorlistedit;
        //   this.$message({
        //     message: '',
        //     type: 'success'
        //   });
        // },
        // submitForm (index,) {
        //   this.updateDoctor();
        //   console.log('', this.form);
        //   this.shiftList[this.Index] = this.form;
          // this.updateDoctor();
          // this.$refs[formName].validate((valid) => {
          //   if (valid) {
          //     this.$message({
          //       message: '',
          //       type: 'success'
          //     });
          //     this.dialogFormVisible = false;
          //   } else {
          //     this.$message.error('');
          //     return false;
          //   }
          // });
        // resetForm (formName) {
        //   this.$refs[formName].resetFields();
        //   alert(99);
        // },
        handleSizeChange (val) {
          console.log(` ${val} `);
        },
        handleCurrentChange (val) {
          console.log(`: ${val}`);
        }
      },
      created () {
        this.$http.get('../../static/doctor/shiftList.json').then((response) => {             // mark
          this.shiftList = response.body.shiftList;
        }, response => {
          // error callback
          alert('');
        });
      }
    };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.shiftDoctor .block
  position:absolute


</style>



